.App {
  overflow: auto;
  font-family: 'Quicksand', sans-serif;
  font-size: 14px;
  color: #767071;
  /*background-image: url('luke-bg.jpg');*/
  background-size: 100%;
}

.Menu {
  color: #363535;
  text-align: center;
}

.menu__link {
  position: relative;
  width: 118%;
  margin-left: -18%;
  padding: 20px 0 20px 0;
  border-bottom: 1px solid #eee;
}

.menu__link:last-of-type {
  border-bottom: none;
}

.cross {
  display: inline-block;
  position: absolute;
  top: 19px;
  left: 50px;
  color: #ea394f;
  font-weight: 700;
  opacity: 0;
  -webkit-transform: rotate(-275deg);
      -ms-transform: rotate(-275deg);
          transform: rotate(-275deg);
  -webkit-transition: opacity 1s, left 1s, -webkit-transform 1s;
  transition: opacity 1s, left 1s, -webkit-transform 1s;
  -o-transition: opacity 1s, left 1s, transform 1s;
  transition: opacity 1s, left 1s, transform 1s;
  transition: opacity 1s, left 1s, transform 1s, -webkit-transform 1s;
}

.menu__link:hover .cross, .cross--active {
  -webkit-transform: rotate(0);
      -ms-transform: rotate(0);
          transform: rotate(0);
  left: 90px;
  opacity: 1;
}

.IntroPanel {
  width: 230px;
  text-align: center;
  padding: 20px 0px 20px 20px;
  margin-top: 70px;
  border-right: 1px solid #eee;
}

.intro-text {
  margin-right: 18%;
  color: #ea394f;
  font-weight: 500;
  font-size: 14px;
  font-style: italic;
}

.regular {
  font-weight: 300;
  color: #625c5c;
}

.CircleImage {
  text-align: center;
  margin-right: 18%;
}

.CircleImage img {
  width: 160px;
}

.AboutPanel {
  overflow: auto;
  padding: 180px 0;
}

.jslogo {
  width: 100%;
}

.container {
  padding: 0 20px;
}

@media (min-width: 768px) {
  .container {
    padding: 0;
    margin: 0 auto;
    width: 85%;
  }
}

@media (min-width: 992px) {
  .container {
    padding: 0;
    margin: 0 auto;
    width: 80%;
  }
}

.col-8 {
  float: left;
  width: 60.3333333%;
  margin-left: 6%;
}

.col-6 {
  float: left;
  width: 50%;
}

.col-4 {
  float: left;
  width: 33.3333333%;
}

.col-3 {
  float: left;
  width: 22%;
}



